<template>
  <div>
    <div v-if="!item.self" class="flex">
      <div class="fx-pc" style="width: 50px;">
        <HeadImg :item="headImgMap[item.clientName]"></HeadImg>
      </div>
      <div class="fx-ver" style="width: 100%">
        <div style="height: 20px;">
          <span class="fz12">{{ nickNameMap[item.clientName] || item.clientName }}</span>
        </div>
        <div>
          <div class="msg">
            <img v-if="item.img" :src="item.img" alt="" width="100%">
            <a v-else-if="item.file" :href="item.file" @click="download($event,item)">{{ item.msg.fileName }}</a>
            <span v-else style="white-space:pre-wrap;">{{ item.msg }}</span>
          </div>
        </div>
      </div>
    </div>
    <div v-if="item.self" class="flex">
      <div class="msgBox fx-pe fx-ac">
        <div class="msg fr">
          <img v-if="item.img" :src="item.img" alt="" width="90%">
          <a v-else-if="item.file" :href="item.file" @click="download($event,item)">{{ item.msg.fileName }}</a>
          <span v-else style="white-space:pre-wrap;">{{ item.msg }}</span>
        </div>
      </div>
      <div class="fx-pc" style="width: 50px;" @click="openDialog">
        <HeadImg :item="headImgMap[item.clientName]"></HeadImg>
      </div>
    </div>
  </div>
</template>

<script>
import client from "../../utils/chatRoom/ConnectWebSocket";
import HeadImg from "./HeadImg";

export default {
  name: "Message",
  props: ['item'],
  components: {HeadImg},
  data() {
    return {
      nickNameMap: client.getNickNameMap(),
      headImgMap: client.getHeadImgMap(),
    }
  }
  ,
  methods: {
    download(ev, item) {
      ev.preventDefault();
      if (typeof item.file !== "string") return
      const link = document.createElement('a');
      link.href = item.file;
      link.download = item.msg.fileName;
      link.click();
      URL.revokeObjectURL(item.file);
      link.remove();
    },
    openDialog() {
      this.$parent.$parent['openDialog']('headImg');
    }
  }
}
</script>

<style scoped>
@import "../../assets/chatroom.css";
</style>